<template>
  <div class="container">
    <el-breadcrumb separator=">" class="title">
      <el-breadcrumb-item :to="{ path: '/ContactInfo' }">通讯信息</el-breadcrumb-item>
    </el-breadcrumb>
    <el-table
      :data="tableData"
      style="width: 100%">
      <el-table-column
        type="index"
        width="50">
      </el-table-column>
      <el-table-column
        prop="name"
        label="姓名"
        >
      </el-table-column>
      <el-table-column
        prop="address"
        label="通讯地址"
        >
      </el-table-column>
      <el-table-column
        prop="company"
        label="工作单位"
        >
      </el-table-column>
      <el-table-column
        prop="department"
        label="部门或院系"
        >
      </el-table-column>
      <el-table-column
        prop="phone"
        label="手机/座机"
        >
      </el-table-column>
      <el-table-column
        prop="email"
        label="E-mail"
        >
      </el-table-column>
      <el-table-column
        prop="detail"
        label="详情"
        >
        <template slot-scope="scope">
          <a @click="lookDetail(scope.row.detail)" href="javascript:void(0)">点击查看</a>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="currentPage"
      :page-sizes="[5, 10, 15, 20]"
      :page-size="10"
      layout="total, sizes, prev, pager, next, jumper"
      :total="200">
    </el-pagination>
  </div>
</template>

<script>
export default {
  name: 'ContactInfo',
  data () {
    const tableData = [{
      name: '小冷',
      address: '北京市朝阳区',
      company: '360',
      department: '企业安全',
      phone: '13657215665',
      email: '896054682@qq.com',
      detail: '啦啦啦啦啦',
    }, {
      name: '小冷',
      address: '北京市朝阳区',
      company: '360',
      department: '企业安全',
      phone: '13657215665',
      email: '896054682@qq.com',
      detail: '啦啦啦啦啦',
    }]
    return {
      tableData: tableData
    }
  },
  methods: {
    handleSizeChange (val) {
      console.log(`每页 ${val} 条`)
    },
    handleCurrentChange (val) {
      console.log(`当前页: ${val}`)
    },
    lookDetail (val) {
      this.$alert(val, '通讯详情', {
        confirmButtonText: '确定'
      })
    }
  }
}
</script>

<style lang="less" scoped>
.container {
  .box-card {
    margin: 20px;
  }
  .title{
    padding-left: 20px;
    height: 45px;
    line-height: 45px;
    border-bottom: 1px solid #ddd;
  }
}
</style>




