<template>
  <div class="container">
    <el-breadcrumb separator=">" class="title">
      <el-breadcrumb-item :to="{ path: '/helpCenter' }">帮助中心</el-breadcrumb-item>
    </el-breadcrumb>
    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <span>常见问题汇总</span>
      </div>
      <div class="text item">
        <h3>Q1:如何投稿？</h3>
        <p>在作者投稿一栏进行投稿操作。</p>
      </div>
      <div class="text item">
        <h3>Q2:如何撤稿？</h3>
        <p>暂时不提供撤稿功能，之后我们会尽快完善，拟出现在作者投稿中的“我的投稿”。</p>
      </div>
      <div class="text item">
        <h3>Q3:如何联系主编？</h3>
        <p>投稿之后可在通讯信息中查看相应人员联系方式。</p>
      </div>
    </el-card>
  </div>
</template>

<script>
export default {
  name: 'HelpCenter',
  data () {
    return {

    }
  }
}
</script>

<style lang="less" scoped>
.container {
  .box-card {
    margin: 20px;
  }
  .title{
    padding-left: 20px;
    height: 45px;
    line-height: 45px;
    border-bottom: 1px solid #ddd;
  }
   .item {
    margin-bottom: 20px;
    font-size: 14px;
    p {
      max-width:1000px;
      font-size:16px;
      a {
        text-decoration: none;
      }
    }
  }
}
</style>




